@import url(https://fonts.googleapis.com/css2?family=Acme&family=Indie+Flower&family=Lato&family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&family=Indie+Flower&family=Lato&family=Open+Sans&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a {
    text-decoration: none;
}

* {
    box-sizing: border-box;
}
html, body, #root, .app, .main {
    height: 100%;
    width: 100%;
}
  
.app {
    background-image: radial-gradient( rgb(55, 11, 143), rgb(0, 0, 0));
}

.main {
    display: flex;
    justify-content:flex-start;
    align-items: center;
    flex-direction: column;
}

.logo {
    width: 270px;
    margin-top: 70px;
}

.start {    
    padding: 12px 20px;
    margin-top: 12px;
    opacity: 0;
    border: none;
    box-shadow: inset 0 0 15px  rgb(55, 11, 143), 0 0 30px #021101;
    border-radius: 10px;
    font-family: 'Acme', sans-serif;
    font-size: 20px;
    width: 180px;
    cursor: pointer;
    outline: none;
}

.score {
    font-family: 'Acme', sans-serif;
    font-size: 24px;
    color: white;
    margin-top: 12px;
}

.img {
    width: 100px;
}

.next {
    padding: 12px;
    margin-top: 12px;
    width: 180px;
    box-shadow: inset 0 0 5px rgb(55, 11, 143), 0 0 15px #021101;
    font-size: 20px;
    border: none;
    cursor: pointer;
    outline: none;
    font-family: 'Acme', sans-serif;
    border-radius: 4px;
}

@media(max-width: 500px){
    .logo {
        width: 250px;
        margin-top: 50px;
    }
    .start {
        width: 150px;
    }
}

@media(max-width: 975px){
    .logo {
        margin-top: 80px;
    }
}
.card {
    width: 50%;
}

.question {
    font-family: 'Acme', sans-serif;
    font-size: 26px;
    margin-top: 30px;
    margin-bottom: 15px;
    color: white;
}

.questionsContainer {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.questionButton {
    margin: 10px 0;
    padding: 12px;
    box-shadow: inset 0 0 15px rgb(55, 11, 143), 0 0 10px #000;
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    border: none;
    cursor: pointer;
    outline: none;
    border-radius: 4px;
}

.correct {
    box-shadow: inset 0 0 15px#03f610;
    background-color: #a0f8a4;
}

.incorrect {
    box-shadow: inset 0 0 15px #f82c08;
    background-color: #fcb2a5;
}

@media(max-width: 975px){
    .question {
        font-size: 30px;
    }

    .questionButton {
        font-size: 22px;
    }
}
